* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  place-content: center;
  align-items: start;
}

#grid1 {
  place-content: center;
  align-items: start;
  padding: 50px;
  display: grid;
}

#grid2 {
  place-content: center;
  align-items: start;
  display: grid;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

h2 {
  color: #333;
  text-align: center;
  margin: 15px 0 10px;
  font-size: 1.3rem;
}

.form-section {
  background: #fff;
  border-radius: 8px;
  padding: 20px;
  display: block;
  box-shadow: 0 4px 8px #0000001a;
}

input, textarea {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 20px;
}

textarea {
  resize: none;
  height: 80px;
}

button {
  color: #fff;
  background-color: #018d18;
  border: none;
  width: 100%;
  padding: 12px;
  font-size: 20px;
}

button:hover {
  background-color: #05770b;
}

.recipe-list {
  background: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

#recipeList {
  padding: 0;
  list-style: none;
}

#recipeList li {
  text-align: left;
  background: #fff;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 15px;
  box-shadow: 0 2px 4px #0000001a;
}

#recipeList li h3 {
  color: #333;
  margin-bottom: 5px;
}

#recipeList li p {
  color: #555;
  font-size: .9rem;
}

#recipeList li button {
  color: #fff;
  cursor: pointer;
  background-color: #dc3545;
  border: none;
  border-radius: 5px;
  width: 100px;
  margin-top: 5px;
  padding: 8px;
}

#recipeList li button:hover {
  background-color: #c82333;
}

#chatbot-container {
  background: #fff;
  border: 1px solid #ccc;
  width: 300px;
  padding: 10px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  height: 200px;
  margin-bottom: 10px;
  padding: 5px;
  overflow-y: auto;
}

#chat-input {
  width: calc(100% - 60px);
  margin-right: 5px;
  padding: 5px;
}

#send-btn {
  cursor: pointer;
  padding: 5px 10px;
}

.history {
  padding-bottom: 15px;
}

@media (width <= 768px) {
  .container {
    grid-template-columns: 1fr;
  }

  .form-section, .recipe-list {
    width: 90%;
    max-width: none;
  }
}
